html {
  color: #060b35;
  font-family: Roboto, sans-serif;
  font-size: 10px;
}

body {
  margin: 0;
}

h3, p {
  margin: 0;
  padding: 0;
}

.stars {
  justify-content: center;
  align-items: center;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars__star:last-child {
  margin-right: 0;
}

.stars--4 .stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

.card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  padding-inline: 16px;
}

.card__image {
  justify-content: center;
  align-items: center;
  display: flex;
}

.card__imac {
  width: 160px;
  height: 134px;
  padding: 32px 16px 40px;
}

.card__name {
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  color: #616070;
  padding-top: 4px;
  line-height: 14px;
}

.card__popular {
  justify-content: space-between;
  align-items: center;
  padding-top: 16px;
  display: flex;
}

.card__popular-reviews {
  line-height: 14px;
}

.card__selling {
  justify-content: space-between;
  padding-top: 24px;
  display: flex;
}

.card__selling-price {
  color: #616070;
  font-size: 12px;
  line-height: 18px;
}

.card__selling-cost {
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button-buy {
  text-transform: uppercase;
  color: #fff;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  height: 40px;
  margin-top: 16px;
  margin-bottom: 16px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  display: flex;
}

.card__button-buy:hover {
  cursor: pointer;
  color: #00acdc;
  background-color: #fff;
}

